/*45vw is total width*/
#save {

	position: absolute;
	left: 12.5vw;
	/*right: 0;*/
	width: 20vw;
	background-color: var(--purple-color);
	color: white;
	font-weight: 800;
	font-size: 1.2em;
	border: 2px solid var(--purple-color);
	box-shadow: 0 0 0 0px rgba(0, 0, 0,.15), 0 8px 16px rgba(0, 0, 0, .15);
	padding: .9em;
	display: none;

}

#save:hover {
	background-color: white;
	color: var(--purple-color);
	border: 2px solid var(--purple-color);

}