#prizing {
	display: flex;
	justify-content: center;
	text-align: center;
	margin-top: 6em;
}

.tier {
	display: flex;
	flex-direction: column;
	margin: 1em;
	padding: 4em;
	box-shadow: 0 5px 5px -3px rgba(0,0,0,.12), 0 8px 10px 1px rgba(0,0,0,.12), 0 3px 14px 2px rgba(0,0,0,.12);
	/*font-size: 1.2em;*/
	/*font-family: sans-serif;*/

}


.priceTag {
	font-weight: 800;
	color: black;
	font-size: 1em;
}

.priceVal {
	font-weight: 800;
	color: rgb(89, 105, 226);
	color: rgb(226,89,0);
	color: var(--purple-color);
	font-size: 3em;
	margin: 1em;

}

.permonth {
	color: black;
	font-size: .4em ;
	font-weight: 400;
}


.login_price {
	/*margin-top: 4em;*/
  /*bottom: 1em;*/
	font-weight: 800;
	border: 2px solid var(--purple-color);
	border-radius: 10em;
	padding: .9em;
  cursor: pointer;
  color: black;
} 

.login_price:hover {
	color: white;
	background: var(--purple-color);
  text-decoration: none;
}

.nv {
  font-size: 1.5em;
  font-weight: 800;
  padding: 1em;
}


.wm {
  color: red;
}

#raz, .login_price {
	margin-top: 4em;
}

#raz > span > a {
	background: white !important;
    border: 2px solid var(--purple-color) !important;
    
}


#raz > span > a:hover {
	background: var(--purple-color) !important;
	color: white !important;
}

.PaymentButton--dark.svelte-ekc7fv.svelte-ekc7fv {
    color: black !important;
    /* font-size: 20em; */
}


.PaymentButton.svelte-ekc7fv.svelte-ekc7fv {
   border-radius: 10em !important;
   height: auto !important;
   padding: .5em !important;
}

