#alert {

	font-size: 1.1em;
	/*font-family: Oswald;*/
	/*font-weight: 600;*/
	padding: 1em;
	text-align: center;
	width:90vw;
	/*height:80vh;*/
	display:none;
	position: absolute;
	background-color: rgb(245,245,239);
	background-color: var(--alert-back-color);
	color: white;

	vertical-align: middle;
	margin-top: 20vh;
	left:5vw;
	z-index:100000;
	box-shadow: 0 5px 5px -3px rgba(0,0,0,.12), 0 8px 10px 1px rgba(0,0,0,.12), 0 3px 14px 2px rgba(0,0,0,.12);

}

#alert > * {
	/*align-self: center;*/
}


.textalert {
	font-weight: 800;
	font-size: 1.2em;
}


.select_alert {
	display: flex;
	justify-content: center;
}

.select_alert > button {
	margin:1em;
}

#addU {
	font-size: 5em;
	color: yellow;
}

#alert > .up-try > .file-chooser > .custom-file-upload {
	color: black !important;
	background-color: white !important;
	border: 1px solid white !important;
}

#alert > .up-try > .file-chooser > .custom-file-upload:hover {
	color: white !important;
	background-color: black !important;
	border: 1px solid black !important;
}