.home-upload {
	font-weight: 600;
	font-size: 24px !important;

	/*font-family: Open Sans,sans-serif;*/
	line-height: 60px;

	display: table-cell;
	border-radius: 4px !important;

	width: 200px;
	height: 60px;
	padding: 6px;

	color: white;

	background-color: #5969e2;
	/*pink from faxonline.app */
	background-color: #ee0979;


	border-color: #ee0979;
