	box-shadow: 1px 1px 1px rgba(22,22,22,.1);
	outline: none;

	margin-bottom: 10px;

}

.home-link {

	/*width: 300px;*/

	font-size: 24px !important;

	padding: 16px 30px 16px 16px;
	margin: 0 !important;
	height: 72px !important;
	vertical-align: top;

	width: 100%;

	border-radius: 4px;
	border: 1px solid #e7e9eb;
	box-sizing: border-box;
	outline: none;
	/*font-size: 18px;*/
	line-height: 24px;
	color: #30353a;
	padding-left: 10px;
	padding-right: 10px;
	margin: 10px 10px 10px 0;
	transition: .2s;

}

.home-link:focus {
	border: 1px solid #5969e2;
	box-shadow: 6px 6px 6px rgba(22,22,22,.1);
}

.home-upload:hover {
	background-color: rgba(89,105,226,0.6);
	color: white;
}

