#ycolumn {
	display: none;
    /*display: block;*/
    z-index: 3000;
    /*position: absolute;*/
    position: fixed;
    /*width: 15%;*/

    top: 20em;
    left: 45vw;
    height: auto !important;
    width: auto !important;
    cursor: grab;
}

#ycolumn:hover {
    box-shadow: .5px .5px .5px;
}

.color-picker .vc-saturation, .color-picker .vc-sketch-saturation-wrap {
    height: 120px !important;
    padding-bottom: 8px;
}

.mt-1 {
    margin-top: .25rem;
}


.vc-sketch {
    /*position: absolute;*/
    width: 25vw;
    padding: 1em;
    /*box-sizing: initial;*/
    background: #fff;
    margin: auto;
    left: 0;
    /*border-radius: 4px;*/
    box-shadow: 0 0 0 1px rgba(0, 0, 0, .15), 0 8px 16px rgba(0, 0, 0, .15);
}


.w-full {
    /*width: 100%;*/
}

.color-picker .vc-sketch-saturation-wrap {
    border-radius: .25rem;
    box-shadow: 0 1px 3px 0 rgba(0,0,0,.1),0 1px 2px 0 rgba(0,0,0,.06);
}

.vc-sketch-saturation-wrap {
    width: 100%;
    padding-bottom: 75%;
    position: relative;
    overflow: hidden;
}


.vc-saturation, .vc-saturation--white, .vc-saturation--black {
    cursor: pointer;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}

/* to show selected color gradient */
.vc-saturation--white {
    background: linear-gradient(to right, #fff, rgba(255,255,255,0));
}

.vc-saturation--black {
    background: linear-gradient(to top, #000, rgba(0,0,0,0));
}

.vc-saturation-pointer {
    cursor: pointer;
    position: absolute;
}

.vc-saturation-circle {
    top: 18px;
    left: 75px;

    cursor: head;
    width: 4px;
    height: 4px;
    box-shadow: 0 0 0 1.5px #fff, inset 0 0 1px 1px rgba(0,0,0,.3), 0 0 1px 2px rgba(0,0,0,.4);
    border-radius: 50%;
    transform: translate(-2px, -2px);
}

.vc-hue-picker {
    cursor: pointer;
    margin-top: 1px;
    width: 4px;
    border-radius: 1px;
    height: 8px;
    box-shadow: 0 0 2px rgba(0, 0, 0, .6);
    background: #fff;
    transform: translateX(-2px);
}


.vc-sketch-controls {
    display: flex;
}

.vc-sketch-sliders {
    padding: 10px 0;
    flex: 1;
}

.vc-sketch-color-wrap {
    width: 24px;
    height: 24px;
    position: relative;
    margin-top: 4px;
    margin-left: 4px;
    border-radius: 3px;
}

.vc-sketch-hue-wrap {
    position: relative;
    height: 10px;
}


.vc-sketch-sliders .vc-hue{
    border-radius: 2px;
}
.vc-hue--horizontal {
/*#f00 red 0%, #ff0 yellow, #0f0 green, #0ff cyan, #00f blue, #f0f pink */
    background: linear-gradient(to right, #f00 0%, #ff0 17%, #0f0 33%, #0ff 50%, #00f 67%, #f0f 83%, #f00 100%);
    background: -webkit-linear-gradient(to right, #f00 0%, #ff0 17%, #0f0 33%, #0ff 50%, #00f 67%, #f0f 83%, #f00 100%);
}
.vc-hue {
    position: absolute;
    top: 0px;
    right: 0px;
    bottom: 0px;
    left: 0px;
    border-radius: 2px;
}

.vc-hue-container {
    cursor: pointer;
    margin: 0 2px;
    position: relative;
    height: 100%;
}

.vc-sketch-active-color {
    position: absolute;
    /*top: 0;*/
    /*left: 0;*/
    /*right: 0;*/
    /*bottom: 0;*/
    /*border-radius: 2em;*/
    box-shadow: inset 0 0 0 1px rgba(0, 0, 0, .15), inset 0 0 4px rgba(0, 0, 0, .25);
    z-index: 2;

    width: 3em;
    height: 3.5em;
}

.vc-sketch-color-wrap .vc-checkerboard {
    background-size: auto;
}
.vc-checkerboard {

/* absolute disables the input tag?*/
    /*position: absolute;*/
    top: 0px;
    right: 0px;
    bottom: 0px;
    left: 0px;
    /*background-size: contain;*/
}

.vc-sketch-field {
    display: flex;
    padding: 2em;
}

.vc-sketch-field--double {
    flex: 2;
}

.vc-sketch-field--single {
    flex: 1;
    padding-left: 2em;
}

.vc-editable-input {
    position: relative;
}

.color-picker .vc-input__input {
    /*border-radius: .25rem;*/
    border-style: none;
    --bg-opacity: 1;
    background-color: #e2e8f0;
    background-color: rgba(226,232,240,var(--bg-opacity));
    box-shadow: none;

    /* this moves the cursor to end of box */
    /*text-align: right;*/
    padding-right: .5rem;
}

.vc-sketch-field .vc-input__input {
    width: 4em;
    height: 3em;
    padding: 4px 0 3px 10%;
        padding-right: 0px;
    border: none;
    box-shadow: inset 0 0 0 1px #ccc;
    font-size: 1em;
    font-weight: 600;
}

button, input, select {
    /*padding: 0;*/
    line-height: inherit;
    color: inherit;
}
button, input {
    overflow: visible;
}
button, input, select {
    font-family: inherit;
    font-size: 100%;
    line-height: 1.15;
    margin: 0;
}


