/* dropdown css */

/* Style The Dropdown Button */
.dropbtn {
  background-color: #4CAF50;
  color: white;
  /*padding: 16px;*/
  /*font-size: 16px;*/
  /*border: none;*/

  /*width: 130px;*/
  cursor: pointer;

      border: none;
     outline:none;
}

/* The container <div> - needed to position the dropdown content */
.dropdown {
  position: relative;
  display: inline-block;
}

/* Dropdown Content (Hidden by Default) */
.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  width: 25em;
  
  max-height: 20em;
  overflow-y: auto;
  /*min-width: 160px;*/
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 4;
}

/* Dropdown Content (Hidden by Default) */
/* 1 2 3 ... */
.dropdown-contenx {
  display: none;
  position: fixed;
  background-color: #f9f9f9;
  width: 5em;
  
  max-height: 20em;
  overflow-y: auto;
  /*min-width: 160px;*/
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 400;
}

/* Links inside the dropdown */
.dropdown-content a, .dropdown-contenx a {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}


/* change color on hover*/

/* Change color of dropdown links on hover */
.dropdown-content a:hover,.dropdown-contenx a:hover,

/* Change the background color of the dropdown button when the dropdown content is shown */
.dropdown:hover .dropbtn, .sidenav button:hover {
  background-color: rgba(0,0,0,0.3);
  color: white;
}

.sidenav button:hover {
  
      border: none;
     outline:none;
}

.clickedH, .clickedS {
      background-color: rgba(0,0,0,1) !important;
    color: white !important;
        border: none;
     outline:none;
    /*border: 1px solid rgba(0,0,0,0.1) !important;*/
}

/* Show the dropdown menu on hover */
.dropdown:hover .dropdown-content {
  display: block;
      border: none;
     outline:none;
}

.dropdown:hover .dropdown-contenx {
  display: block;
      border: none;
     outline:none;
}


