.slider {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  /*width: 300px;*/
  height: 1em;
  background-color: rgb(205,205,205);
  z-index: 10;
}



.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  border: 2px solid red;
  border-radius: 20%;
  height: 20px;
  width: 20px;
  /*position: relative;*/
  position: absolute;
  bottom: 8px;
  background: #222;
  background: yellow;
  background-size: 50%;
  box-shadow: 0px 3px 5px 0px rgba(0, 0, 0, 0.4);
  cursor: grab;

  color: red;
    &:active {
      cursor: grabbing;
    }
}

.slider::-moz-range-thumb {
  -moz-appearance: none;
  appearance: none;
  border: 1px solid grey;
  border-radius: 50%;
  height: 10px;
  width: 10px;
  /*position: relative;*/
  position: absolute;
  bottom: 8px;
  background: white;
  /*background-size: 50%;*/
  box-shadow: 0px 3px 5px 0px rgba(0, 0, 0, 0.4);
  cursor: grab;

    &:active {
      cursor: grabbing;
    }
}


