.slidernone {

    /*width: 200px !important;*/
    z-index: 10;
    height: 50px;

    margin-top: 22px;
    margin-left: 15px;

    /*position: absolute;*/
    width:70%;
    /*background-color:#8DA5C6;*/
    z-index: 999;


}
.sliderno {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  /*width: 300px;*/
  height: 4px;
  background-color: rgb(205,205,205);
  z-index: 10;
}



.sliderno::-webkit-slider-thumb {
  -webkit-appearance: none;
 
  appearance: none;
  border: 1px solid grey;
  border-radius: 50%;
  height: 15px;
  width: 10px;
  /*position: relative;*/
  /*position: absolute;*/
  bottom: 8px;
  background: white;
  /*background-size: 50%;*/
  box-shadow: 0px 3px 5px 0px rgba(0, 0, 0, 0.4);
  cursor: grab;

  color: red;
    &:active {
      cursor: grabbing;
    }
}

.sliderno::-moz-range-thumb {
  -moz-appearance: none;
  appearance: none;
  border: 1px solid grey;
  border-radius: 50%;
  height: 13px;
  width: 10px;
  /*position: relative;*/
  position: absolute;
  bottom: 8px;
  background: white;
  /*background-size: 50%;*/
  box-shadow: 0px 3px 5px 0px rgba(0, 0, 0, 0.4);
  cursor: grab;

    &:active {
      cursor: grabbing;
    }
}



