#onlystyle {

    /*width: 8vw;*/
    margin-top: 4vw;
    display: flex;
    flex-direction: column;
    /*right: 0vw;*/
    position: fixed;
    align-items: center;

}


#onlystyle > * {
	position: relative;
	width: 7.3em;
    height: 8em;
    /*font-size: 1em;*/
    color: rgba(0,0,100,0.7);
    padding: 1em;
    margin: .1em;
    margin-bottom: .1em;
    box-shadow: 0 0 0 0px rgba(0, 0, 0,.15), 0 8px 16px rgba(0, 0, 0, .15);
}

#onlystyle > button:hover {
	font-weight: 600;
	color: rgb(0,0,0);
}

#onlystyle > button:focus {
	font-weight: 600;
	color: rgb(0,0,0);
}

.clickedV {
	font-weight: 600;
	color: rgb(0,0,0) !important;
}

#onlystyle .MuiSvgIcon-root {
	font-size: 3em; 
}



