#lang {
	z-index: 10000000;
	position: absolute;
	display: none;

box-shadow: 0 5px 5px -3px rgba(0,0,0,.12), 0 8px 10px 1px rgba(0,0,0,.12), 0 3px 14px 2px rgba(0,0,0,.12);


}

.langalert {
	width: 100vw;
	height: 92vh;
	font-size: 1.2em;
	font-weight: 600;
  	box-shadow: 0 5px 5px -3px rgba(0,0,0,.12), 0 8px 10px 1px rgba(0,0,0,.12), 0 3px 14px 2px rgba(0,0,0,.12);

	background: rgba(0,0,20,0.85);
	color: white;
	/*margin: 2em;*/
	padding: 5em;
	/*margin-top: 5vh;*/
	left: 0vw;
	position: absolute;
	text-align: center;
	top: 3vh;
}

.langinput {
	display: flex;
	width: 50vw;
	background: var(--purple-color);
	cursor: pointer;
	left:25vw;
	position: absolute;
	top: 20vh;
}

.input-lang:focus {
	outline: none;
}

.input-lang {
	width: 47vw;
	border: none !important;
	border-bottom: 1px;
	cursor: pointer;
	padding: 1em;

	/*background: rgba(0,0,0,0.3);*/
}

#langselect > * {
	/*border: .01em dashed grey;*/
	/*border-bottom: none;*/
	color: black;
	font-size: 1.1em;
	/*text-align: left;*/
	text-align: center;
	/*height: 2.5em;*/
	/*width: 10em;*/
	padding: 1em;
box-shadow: 0 5px 5px -3px rgba(0,0,0,.12), 0 8px 10px 1px rgba(0,0,0,.12), 0 3px 14px 2px rgba(0,0,0,.12);

}

#langselect {
	display: flex;
	flex-direction: column;
	/*background: white;*/
