@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,800);

// $background:#f9f9f9;
$background: white;  //color in b/w stop bars
// $foreground:#2c3e50;
$foreground: black;   //color of button 

// $foreground-light:#34495e;
$foreground-light: grey;


$size:20px;
$ratio:1.3;

$transition-time:0.3s;


.play {
 // background: black;  // between play button and circle

  display:block;
  top: -20px;
  width: 0; 
  height: 0; 
  border-top: $size solid transparent;
  border-bottom: $size solid transparent;
  border-left: ($size*$ratio) solid $foreground;
  margin: ($size * 2) auto $size auto;
  position:relative;
  z-index:1;
  transition: all $transition-time;
  -webkit-transition: all $transition-time;
  -moz-transition: all $transition-time;
  left:($size*0.2);
  

  // this is css for circle
  &:before {
    content:'';
    position:absolute;
  // top:($size*-1.5);
  //  left:($size*-1.5);
  //  bottom:($size*-1.5);
  //  right:($size*-0.7);
    
    top:($size*-1.5);
    left:($size*-2.5);
    bottom:($size*-1.5);
    right:($size*-0);

    border-radius:50%;
    border: ($size*0.2) solid $foreground;
    z-index:2;
    transition: all $transition-time;
    -webkit-transition: all $transition-time;
    -moz-transition: all $transition-time;
  }
  &:after {
    content:'';
    opacity:0;
    transition: opacity ($transition-time * 2);
    -webkit-transition: opacity ($transition-time * 2);
    -moz-transition: opacity ($transition-time * 2);
  }
  
  &:hover, &:focus {
    &:before {
       transform: scale(1.1);
       -webkit-transform: scale(1.1);
       -moz-transform: scale(1.1);
    }
  }
  
  &.active {  
  // the stop button
    border-color:transparent;
    &:after {
    // the stop symbol css
      content:'';
      opacity:1;
      width:($size);
      height:($size*1.6);
      background:$foreground;
      position:absolute;
      right: ($size*0.35);
      top: ($size*-0.8);
      left: -26px;
      border-left:($size*0.4) solid $foreground;
      border-right:($size*0.4) solid $foreground;
      box-shadow:inset ($size*0.6) 0 0 0 $background;
    }
  }
}

