#file-input, #file-workspace, #file-home, #file-alert{

  display: none;

}

.custom-file-upload {
  cursor: pointer;
  font-weight: 800;

  /*width: 8em;*/
  /*height: 2em;*/
  font-size: 2em;
  color: var(--main-text-color);
  background-color: var(--purple-color);
  border: .1em solid var(--purple-color);
  padding: .5em;
  padding-right: 2em;
  padding-left: 2em;
  border-radius: .2em;
  display: unset !important;
  transition: .1s;
}

.custom-file-upload:hover{
  border: .1em solid var(--purple-color);
  background-color: var(--main-text-color);
  color: var(--purple-color);
}

