
.slidercontainer {
  width: 96%; /* Width of the outside container */
}



 /*excess height to improve interactive area / accessibility*/

/* background height*/
$height: 30px;
$thumb-height: 15px;
$track-height: 3px;


 /*colours*/

/* color of right half of slider*/
$upper-color: grey ;
/*$upper-color: #cfcfcf;*/


/*$lower-color: #0199ff;*/
/* left half color*/
$lower-color: black;
$lower-hover-color: black;
/*$thumb-color: #0199ff;*/
/* color of the cursor */
$thumb-color: black;  


$thumb-hover-color: black;

$back-color: transparent;
$thumb-hover-back-color: black;

$upper-background: linear-gradient(to bottom, $upper-color, $upper-color) 100% 50% / 100% $track-height no-repeat transparent;
/*$upper-background:green;*/
$lower-background: linear-gradient(to bottom, $lower-color, $lower-color) 100% 50% / 100% $track-height no-repeat transparent;
/*$lower-background: white;*/

 /*Webkit cannot style progress so we fake it with a long shadow*/
/*on the thumb element*/
@function webkit-slider-thumb-shadow($i: 1) {
  $val: #{$i}px 0 0 -#{($thumb-height - $track-height) / 2} #{$upper-color};
  @if $i == 1 {
    @for $k from 2 through 1000 {
      $val: #{$val}, webkit-slider-thumb-shadow($k);
    }
  }
  @return $val;
}

.slide {
  display: block;
  appearance: none;
  width: 100%;
  margin: 0;
  height: $height;
  overflow: hidden;
  cursor: pointer;
  background: $back-color;
  
  &:focus {
    outline: none;

  }
}

/*Webkit*/
.slide::-webkit-slider-runnable-track {
  width: 100%;
  height: $height;
  background: $lower-background;
}

.slide::-webkit-slider-thumb {
  position: relative;
  appearance: none;
  height: $thumb-height;
  width: $thumb-height;
  background: $thumb-color;
  border-radius: 100%;
  border: 0;
  top: 50%;
  margin-top: (-$thumb-height/2);
  box-shadow: webkit-slider-thumb-shadow();
  transition: background-color 150ms;
}

/*Firefox*/
.slide::-moz-range-track,
.slide::-moz-range-progress {
  width: 100%;
  height: $height;
  background: $upper-background;

}

.slide::-moz-range-progress {
  background: $lower-background;
  
}

.slide::-moz-range-thumb {
  appearance: none;
  margin: 0;
  height: $thumb-height;
  width: $thumb-height;
  background: $thumb-color;
  border-radius: 100%;
  border: 0;
  transition: background-color 150ms;
}

 /*Internet Exploder*/
.slide::-ms-track {
  width: 100%;
  height: $height;
  border: 0;
   /*color needed to hide track marks*/
  color: transparent;
  background: transparent;
}

.slide::-ms-fill-lower {
  background: $lower-background;

}

.slide::-ms-fill-upper {
  background: $upper-background;
}

.slide::-ms-thumb {
  appearance: none;
  height: $thumb-height;
  width: $thumb-height;
  background: $thumb-color;
  border-radius: 100%;
  border: 0;
  transition: background-color 150ms;
  /*IE Edge thinks it can support -webkit prefixes*/
  top: 0;
  margin: 0;
  box-shadow: none;
}

.slide:hover,
.slide:focus {

 opacity: 1;

  &::-webkit-slider-thumb {
    background-color: $thumb-hover-color;
/*    background: $thumb-hover-back-color;*/
  }
  &::-moz-range-thumb {
    background-color: $thumb-hover-color;
/*    background: $thumb-hover-back-color;*/
  }
  &::-ms-thumb {
    background-color: $thumb-hover-color;
/*    background: $thumb-hover-back-color;*/
  }
}

