.xcontainer {
/*  max-width: 25em; */

    width: 25em;

    left: 0;
    right: 0;
    margin: auto;
    margin-top: 5em;
}

#comparison {
  width: 100%;
  padding-bottom: 100%;
  /*overflow: hidden; */
  position: relative;
  height: 40em;
}

figure { 
  position: absolute;
  background-image: url(pop1.png);
  background-size: cover;
  font-size: 0;
  width: 100%;

  /*frame is square, to show full width*/
  height: 90%;
  margin: 0; 
}

#divisor { 
  background-image: url(pop2.png);
  background-size: cover;
  position: absolute;
  width: 50%; 
  box-shadow: 0 5px 10px -2px rgba(0,0,0,0.3);
  bottom: 0; height: 100%;
  
  &::before,
  &::after {
    content: '';
    position: absolute;
    right: -2px;
    width: 4px;
    height: calc(50% - 25px);
    background: white;
    z-index: 3;
  }
  &::before {
    top: 0;
    box-shadow: 0 -3px 8px 1px rgba(0,0,0,.3);
  }
  &::after {
    bottom: 0;
    box-shadow: 0 3px 8px 1px rgba(0,0,0,.3);
  }
}
#handle {
  position: absolute;
  height: 50px;
  width: 50px;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);
  z-index: 1;
  
  &::before,
  &::after {
    content: '';
    width: 0;
    height: 0;
    border: 6px inset transparent;
    position: absolute;
    top: 50%;
    margin-top: -6px;
  }
  &::before {
    border-right: 6px solid white;
    left: 50%;
    margin-left: -17px;
  }
  &::after {
    border-left: 6px solid white;
    right: 50%;
    margin-right: -17px;
  }
}


#xslider{
  -webkit-appearance:none;
  -moz-appearance:none;
  position: absolute;
  
 /*bcoz of 90% height */
  top: 45%; 
  left: -25px;
  transform: translateY(-50%);
  background-color: transparent;
  width: calc(100% + 50px); 
  z-index: 2;
  
  cursor: pointer;
  
  &:focus,
  &:active {
    border: none;
    outline: none;
  }
}


#xslider::-webkit-slider-thumb {
  -webkit-appearance: none;
  border: none;
  height: 50px;
  width: 50px;
  border-radius: 50%;
  background: transparent;
  border: 4px solid white;
  box-shadow: 0 0 8px 1px rgba(0,0,0,.3);
}

#xslider::-moz-range-track {
  -moz-appearance:none;
  height:15px;
  width: 100%;
  background-color: transparent; 
  position: relative;
  outline: none;    
}